import { useAuth0 } from "@auth0/auth0-react";
import { useContext, useEffect, useState } from "react";
import styled, { css } from "styled-components";
import Styles, { _CSS_LABEL_MIDIUM } from "../../base/design/styles";
import DataUtil from "../../utils/dataUtil";
import { GlobalContext } from "../entry/systemEntry";

type scoreProps = {
    dispnam: string;
    seq: number;
    owner: string;
    lastmod: string;
}

const HomeManage = () => {

    const { isAuthenticated, user, isLoading } = useAuth0();
    const { store, dispatcher } = useContext(GlobalContext);

    const [userId, setUserId] = useState('-');
    const [userCount, setUserCount] = useState(0);

    const [scoreList, setScoreList] = useState<scoreProps[]>([]);

    const loginProps = store.userEnv.login;

    let email = '-';
    if (isAuthenticated && user != undefined) {
        email = user.email ?? '-';
    }

    useEffect(() => {
        if (store.systemState.directScore == null) {
            countUsers().then(cnt => {
                setUserCount(24 + cnt);
                findAllScore().then((resList) => {
                    setScoreList(resList as scoreProps[]);
                });
            });
        }
    }, []);

    useEffect(() => {
        if (!loginProps) {
            setUserId('');
        } else {
            setUserId(loginProps.id);
        }
    }, [loginProps, isAuthenticated]);

    const createUser = () => {
        if (user != undefined) {
            DataUtil.sendQueryRequestToAPI('update', `INSERT INTO user_tbl(email, id) VALUES('${user.email}', '${userId}')`).then(() => {
                window.location.reload();
            });
        }
    }

    const updateUser = () => {
        DataUtil.sendQueryRequestToAPI('update', `UPDATE user_tbl SET id = '${userId}' WHERE seq = ${loginProps?.seq}`).then(() => {
            window.location.reload();
        });
    }

    const registerLabel = loginProps == undefined ? 'create' : 'update';
    const registerAction = loginProps == undefined ? createUser : updateUser;

    const scoreListJsx = scoreList.map((score, i) => {
        return (
            <_CloudItem key={i} onClick={() => {
                window.open(DataUtil.getPreviewURL(score.seq), '_blank');
            }}>
                <_Owner>{`${score.owner}`}</_Owner>
                <_CName>{`${score.dispnam}`}</_CName><_CDate>{score.lastmod}</_CDate>
            </_CloudItem>
        );
    });

    return (<_Wrap>
        <_Left>
            <_MessageRecord>{userCount} users have created accounts.</_MessageRecord>
            <_Recored>
                <_TitleLabel>User Data</_TitleLabel>
            </_Recored>
            <_ListInfoDiv isEnable={isAuthenticated && !isLoading}>
                <_Recored>
                    <_ItemnLabel>e-mail</_ItemnLabel>
                    <_ItemnValue>{email}</_ItemnValue>
                </_Recored>
                <_Recored>
                    <_ItemnLabel>user id</_ItemnLabel>
                    <_ItemnForm type="text" value={userId} onChange={(e) => {
                        setUserId(e.target.value);
                    }} />
                </_Recored>
                <_ButtonsDiv>
                    <_Button isEnable={true} onClick={registerAction}>{registerLabel}</_Button>
                </_ButtonsDiv>
            </_ListInfoDiv>
        </_Left>
        <_Right>
            <_MessageRecord>{scoreList.length} licresia scores have been published.</_MessageRecord>
            <_ListFrame>{scoreListJsx}</_ListFrame>

        </_Right>
    </_Wrap>);
}

export default HomeManage;


export const countUsers = async () => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `SELECT count(*) cnt FROM user_tbl`);
    const results = await response.json();
    return (results as any[])[0]['cnt'] as number;
};

export const findAllScore = async () => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `
        SELECT seq, (select id from user_tbl where seq = score.user_seq) as owner, dispnam, lastmod
        FROM scoretbl score
        WHERE share <> 'url'
        ORDER BY lastmod desc
    `);
    const results = await response.json();
    return results as any[];
};

